import React, {
	FC,
	useCallback, useEffect, useRef,
} from "react";

import { Icon } from "../Icon/Icon";

import {
	FilterIconWrapper,
	StyledFilterListItem,
} from "./FilterMenu.styles";
import { FilterItemProps } from "./FilterMenu.types";


const FilterItem: FC<FilterItemProps> = ({
	filter,
	focus,
	setFocus,
	index,
	setSelectedFilter,
	onSelectFilterFunc = () => { },
	closeMenu = () => { },
	$isSelected,
	dataQA,
}) => {
	const ref = useRef<HTMLLIElement>(null);
	const { filterName, filterId, filterComponent, filterAvatar } = filter;

	useEffect(() => {
		if (focus && ref) {
			// Move element into view when it is focused
			ref.current?.focus();
		}
	}, [focus]);


	const handleSelect = useCallback(() => {
		if (filter && filterComponent) {
			setSelectedFilter(filter);
			return;
		}

		if (filter && filterName) {
			onSelectFilterFunc(filterName, filterId);
			closeMenu();
		}

		// setting focus to that element when it is selected
		if (index) {
			setFocus(index);
		}
	// eslint-disable-next-line max-len
	}, [filter, filterName, filterId, filterComponent, index, setFocus, setSelectedFilter, onSelectFilterFunc, closeMenu]);

	const handleOnKeyPress = (event: React.KeyboardEvent<HTMLLIElement>) => {
		if (event.code !== "Enter") {
			return;
		}

		return handleSelect();
	};

	return (
		<StyledFilterListItem
			tabIndex={focus ? 0 : -1}
			key={filterName}
			role="button"
			onClick={handleSelect}
			onKeyPress={handleOnKeyPress}
			ref={ref}
			$isSelected={$isSelected}
			data-qa={`${dataQA}-li`}
		>
			{filterAvatar}
			<span data-qa={dataQA}>{filterName}</span>
			{$isSelected && <FilterIconWrapper><Icon name="Check" /></FilterIconWrapper>}
		</StyledFilterListItem>
	);
};

FilterItem.displayName = "FilterItem";

export default FilterItem;

